<template>
  <div class="our-blog">
    <h1>{{ pageName }}</h1>
    <h1>{{ pageDescription }}</h1>
    <div class="container">
      <div class="row">
        <div class="col-md-8">
          <div class="posts-area">
            <BlogPosts
              v-for="post in posts"
              v-bind:key="post.id"
              v-bind:title="post.title"
              v-bind:author="post.author"
              v-bind:content="post.content"
              v-bind:category="post.title"
              v-bind:date="post.date"
              v-bind:views="post.views"
            />
          </div>
        </div>
        <div class="col-md-4">
          <div class="sidebar">
            <BlogSidebar />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BlogPosts from "@/components/Blog/BlogPosts.vue";
import BlogSidebar from "@/components/Blog/BlogSidebar.vue";
// consider this is the data we got from api
import jsonPosts from "../json/jsonPosts.json";
export default {
  name: "BlogView",
  data: function () {
    return {
      pageName: "Blog",
      pageDescription: "This Blog Page",
      posts: jsonPosts,
    };
  },
  components: {
    BlogPosts,
    BlogSidebar,
  },
};
</script>
